import React from 'react';
import './Regolamento.css';

const Regolamento = () => {
  return (
    <div className="regolamento">
      <h1>Regolamento</h1>
      <p> <h2>REGOLAMENTO URBANIA CLIMBING FESTIVAL - PREMESSA E CARATTERISTICHE GENERALI:</h2>
La manifestazione di Street Boulder si svolge su percorsi brevi di arrampicata, da scalare senza corda posizionando il crash-pad in modo da evitare infortuni, tracciati su facciate di case, muri in pietra
appositamente predisposte e segnalate dell'associazione organizzatrice.
<h2>SICUREZZA ED EQUIPAGGIAMENTO:</h2>
Tutti gli iscritti dovranno avere l'equipaggiamento per la pratica del boulder in sicurezza, minimo un crash-pad ogni due persone. I responsabili delle zone boulder potranno in qualsiasi momento allontanare le persone non munite dei requisiti di sicurezza.
<br></br>I partecipanti dovranno “pararsi” a vicenda, onde evitare infortuni.
<br></br>In circostanze eccezionali, durante lo svolgimento della manifestazione il responsabile del settore potrà
decidere di chiudere e di cancellare uno o più blocchi per preservare l'incolumità degli atleti o l'integrità della parete. Chiunque venga trovato a scalare al di fuori dei percorsi di gara verrà automaticamente squalificato e segnalato alle autorità competenti.
<h2>PRE-ISCRIZIONE AL CONTEST:</h2>
La pre-iscrizione al contest da diritto al pacco gara che comprende: zainetto, maglietta del contest, Freesbee, Buono birra, mappa del contest, braccialetto identificativo. A chi non ha effettuato la pre-iscrizione, non verrà fornito il pacco gara.
<h2>ISCRIZIONE AL CONTEST:</h2>
Le iscrizioni al contest aprono il giorno dell’evento, 16 maggio alle ore 12:00 nel Cortile del Palazzo Ducale presso il gazebo dell’organizzazione.
<br></br>Costo dell’iscrizione al contest € 20,00 per gli ADULTI con pacco gara;
<br></br>Costo dell’iscrizione al contest €20,00 per gli ADULTI senza pacco gara dopo la chiusura delle pre iscrizioni;
<br></br>Eventuali pacchi gara avanzati, verranno assegnati ai primi non pre-iscritti che si presentano all’iscrizione stessa.
<br></br>Tutti gli atleti dovranno compilare il modulo di responsabilità con i propri dati anagrafici. Per i minorenni il modulo dovrà essere compilato da un genitore o tutore legale che dovrà essere presente sia al momento dell’iscrizione, sia durante la manifestazione.
<h2>IMPORTANTE:</h2>
Qualora si sorprendesse qualsiasi persona non munita di braccialetto identificativo/non iscritta al contest arrampicare, o munita di braccialetto ed iscritta al contest arrampicare fuori dalle zone contrassegnate, questa verrà immediatamente allontana, squalificata dal contest e segnalata alle autorità competenti. 
<br></br>E' comunque a discrezione degli organizzatori allontanare o squalificare dal contest le persone che metteranno a repentaglio la propria incolumità o quella degli altri partecipanti.
<h2>IL CONTEST</h2>
Le zone boulder sono specificate sulla mappa e sono a disposizione dei partecipanti e monitorate dai responsabili dell'organizzazione.
<br></br>Tutti i blocchi sono contraddistinti da un'immagine numerata dove viene segnato il percorso del problema. Affinché il blocco sia valido, l'iscritto dovrà partire con le mani sullo start o sit-start e dopo aver sollevato i piedi da terra dovrà raggiungere il top, tenendo la posizione almeno tre secondi.
<br></br>L’inizio del Contest è fissato per le ore 9:00 fino alle 17:00 (l’organizzazione si riserva di poter modificare gli orari).
</p>
    </div>
  );
};

export default Regolamento;